@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow-x: hidden;
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
   user-select: none;
}
.Mobile-enter {
  display: none;
  position: absolute;
  width: 500px;
  height: 200px;
}
.Home {
  width: 100%;
  height: 100%;
  position: absolute;
}
.Flexbox {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.Content-container {
  height: 100%;
}
.Text-container {
  font-family: 'Zilla Slab', serif;
  margin: 60px;
  white-space: nowrap;
}

.Text-container h1 {
  font-size: 100px;
  font-weight: bold;
  line-height: 80px;
  margin: 10px;
}
.Text-container p, h3 {
  font-size: 50px;
  font-weight: normal;
  margin: 10px;
}
.Social {
  display: flex;
  position: absolute;
  bottom: 30px;
}
.Social-icon {
  width: 30px;
  height: 30px;
  margin: 20px;
  transition-duration: 1s;
}
.Social-icon:hover {
  color: #61DAFB;
  transition-duration: 1s;
}

/*Left content*/
.Left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  background-color: #ffffff;
  color: #282c34; 
}
.Left h1 {
  margin-left: 0px;
}
.Linkedin {
  width: 50%;
  justify-content: flex-end;
}
.Linkedin-icon {
  display: block;
  color: #282c34;
  transition-duration: 1s;
}
.Linkedin-icon-dark {
  display: none;
  color: #ECECEC;
}

/*Left animation*/
.Left-animation {
  position: absolute;
  height: 450px;
  width: 350px;
}

/*Right*/
.Right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  background-color: #282c34;
  color: #ECECEC;
  position: relative;
}
.Dark-mode-icon {
  width: 30px;
  height: 30px;
  margin: 20px;
  color: #ECECEC;
}
.Dark-mode {
  z-index: 10;
  position: absolute;
  right: 0px;
  top: 0px;
}
#Dark-on {
  display: block;
}
#Dark-off {
  display: none;
}
.Github {
  width: 50%;
}
.Github a {
  margin-bottom: -4px;
}
.Github-icon {
  color: #ECECEC;
  transition-duration: 1s;
}

/*Right animation*/
.Right-animation {
  position: absolute;
  height: 450px;
  width: 350px;
  right: 0px;
}
.Suport-right {
  display: none;
  position: absolute;
  width: 600px;
  height: 300px;
  margin-top: 70%;
  margin-left: -110%;
}

@media (max-width: 1000px) {
  body {
    position: absolute;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .Flexbox {
    display: flex;
    flex-direction: column;
  }
  .Content-container {
    height: 50%;
  }
  .Left {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    color: #282c34; 
    width: 100% !important;
  }
  .Right {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100% !important;
    background-color: #282c34;
    color: #ECECEC;
  }
  .Social {
    display: none;
  }
  .Dark-mode-icon {
    color: #282c34;
  }
  .Left-animation {
    display: none;
  }
  .Right-animation {
    display: none;
  }
  .Mobile-enter {
    display: block;
  }
}
@media (max-width: 600px) {
  .Text-container h1 {
    font-size: 70px;
    font-weight: bold;
    line-height: 50px;
    margin: 0px;
  }
  .Text-container p, h3 {
    font-size: 35px;
    font-weight: normal;
    margin: 3px 3px 3px 0px;
  }
}
.About{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
#Header {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  z-index: 300;
  width: 100%;
}
.Photo {
  width: 250px;
  height: 250px;
}
.Social-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  width: 100%;
}
.Icons-area {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}
.Icons {
  width: 50px;
  height: 50px;
  margin: 20px 70px 35px 70px;
  color: #282c34;
  transition-duration: 1s;
}
.Icons:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Number-social {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Number {
  font-size: 40px;
  font-weight: bold;
  margin-top: 0px;
}
#Body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.Area-title {
  display: flex;
  justify-content: center;
  width: 50px;
  border-bottom: 3px solid #282C34;
  margin-bottom: 20px;
}

.About-body {
  display: flex;
  flex-direction: row;
  text-align: right;
  width: 55%;
  margin-top: 80px;
}
#Body p {
  font-size: 25px;
  font-family: 'Zilla Slab', serif;
  color: #282c34;
  
}
#Body h2 {
  font-size: 30px;
  font-family: 'Zilla Slab', serif;
  color: #282c34;
  font-weight: bold;
  margin-top: 100px;
  margin-bottom: 20px;
}
#Body span {
  font-size: 35px;
}
.Photo-about {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
}
.Services {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 80px;
  width: 70%;
}
.Services-item {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: left;
  margin: 10px;
}
.Services-item p{
  font-size: 20px !important;
  margin: 5px;
}
.Services-item h3{
  font-size: 20px !important;
  margin: 5px;
}
.Item-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Item-flex p{
  font-weight: bold;
  font-size: 20px !important; 
  margin: 0px;
}
.Icon-service {
  width: 25px;
  height: 25px;
  margin: 10px;
}
#Divisor {
  width: 100%;
  height: 10px;
  margin-top: 50px;
  box-shadow:         0px 6px 5px 0px rgba(50, 50, 50, 0.19);
}
.Area-depoi {
  width: 70%;
}
.Depoi-item {
  text-align: center;
}
.Depoi-item h3 {
  margin-top: 50px;
  font-size: 25px;
}
.Depoi-item p {
  font-style: italic;
  font-size: 20px !important;
}
.Depoi-item img {
  width: 200px;
  height: 200px;
  margin-top: 50px;
}
#Footer {
  text-align: center;
  width: 100%;
  height: 50px;
  margin-top: 50px;
  bottom: 0px;
  background-color: #282c34;
}
#Footer p {
  font-size: 15px;
  color: #ECECEC;
}
.Main-header {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Menu-icon {
  width: 40px;
  height: 40px;
  margin: 15px;
  color: #282C34;
  transition-duration: 1s;
}
.Menu-icon:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
#Main-header-click {
  width: 25%;
  height: 100vh;
  background-color: #282C34;
  transition-duration: 1s;
  position: absolute;
  left: -25%;
  top: 0px;
}
.Menu-icon-click {
  position: absolute;
  width: 40px;
  height: 40px;
  margin: 15px;
  color: #ECECEC;
  transition-duration: 1s;
}
.Menu-icon-click:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.Menu li {
  list-style-type: none;
  margin-bottom: 30px;
  font-size: 25px;
  color: #ECECEC;
}
.Menu ul {
  margin: 0px;
}
.Link {
  text-decoration: none;
  color: inherit;
  transition-duration: 1s;
}
.Link:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Contact{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
}

#Header-contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  z-index: 300;
  width: 100%;
}
#Body-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
#Body-contact p {
  font-family: 'Zilla Slab', serif;
}
.Text-area-contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 80px;
  width: 70%;
}
.Text-contact {
  width: 50%;
}
.Text-contact p {
  text-align: left;
  font-size: 25px;
  color: #282c34;
}
.Text-contact span {
  font-size: 40px;
}
.Image-contact {
  width: 50%;
}
.Contact-icons-area {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 70%;
}
.Icon-contact {
  width: 30px;
  height: 30px;
  margin: 10px;
  color: #282c34;
  transition-duration: 1s;
}
.Icon-contact:hover {
  color: #61DAFB;
  transition-duration: 1s;
}
.Mail-area {
  margin-top: 50px;
}
.Mail-area p {
  font-size: 20px;
  color: #282c34;
}
.Mail-area a {
  text-decoration: none;
  color: inherit;
  font-weight: 700;
  transition-duration: 1s;
}
.Mail-area:hover a {
  color: #61DAFB;
  transition-duration: 1s;
}

#Footer-contact {
  position: absolute;
  text-align: center;
  width: 100%;
  height: 50px;
  margin-top: 50px;
  bottom: 0px;
  background-color: #282c34;
}
#Footer-contact p {
  font-size: 15px;
  color: #ECECEC;
}
.Portfolio{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
}
#Header-portfolio {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  z-index: 300;
  width: 100%;
}
#Body-portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
#Footer-portfolio {
  position: absolute;
  text-align: center;
  width: 100%;
  height: 50px;
  margin-top: 50px;
